.registerButton {
  display: block;
  width: 150px;
  margin: 0 auto;
  padding: 10px 20px;
  background-color: rgba(245, 126, 225, 0.637);
  color: rgb(77, 77, 77);
  border-radius: 10%;
  border: 2px solid rgba(255, 143, 236, 0.637);
}

.registerButton:hover {
  background-color: rgba(247, 101, 222, 0.637);
  color: white;
}

.registerButton:focus {
  background-color: rgba(247, 101, 222, 0.637);
  color: white;
}
.registerInput {
  margin-top: 5px;
  display: block;
  width: 300px;
  padding: 8px;
}
